import Api from '@/plugins/Api'

export default {
  namespaced: true,
  state: {
    items: null
  },
  mutations: {
    getItems (state, data) {
      state.items = data
    },
    addItems (state, data) {
      state.items = data
    }
  },
  actions: {
    async getChecklist ({ commit }, data) {
      const res = await Api('http://18.141.178.15:8080/checklist').get(data)

      commit('getItems', res.data)
      return res
    },
    async saveChecklist ({ commit }, data) {
      const res = await Api('http://18.141.178.15:8080/checklist').post(data)

      return res
    },
    async deleteChecklist ({ commit }, data) {
      const res = await Api('http://18.141.178.15:8080/checklist/' + data).delete()

      return res
    }
  },
  getters: {
    state (state) {
      return state
    }
  }
}
