import Api from '@/plugins/Api'

export default {
  namespaced: true,
  state: {
    youtube: null
  },
  mutations: {
    setYoutubeDatas (state, data) {
      state.youtube = data
    }
  },
  actions: {
    async getOverview ({ commit }, data) {
      const res = await Api('https://www.googleapis.com/youtube/v3/channels').get(data)

      // if (res.success) {
      commit('setYoutubeDatas', res)
      // }

      return res
    }
  },
  getters: {
    state (state) {
      return state
    }
  }
}
