export default {
  namespaced: true,
  state: {
    loading: false
  },
  mutations: {
    loading (state, data) {
      state.loading = data
    }
  },
  actions: {
  },
  getters: {
    loading (state) {
      return state.loading
    },
    state (state) {
      return state
    }
  }
}
