import Api from '@/plugins/Api'

export default {
  namespaced: true,
  state: {
    // youtube: null
  },
  mutations: {
    // setYoutubeDatas (state, data) {
    //   state.youtube = data
    // }
  },
  actions: {
    async signUp ({ commit }, data) {
      const res = await Api('http://18.141.178.15:8080/register').post(data)
      // if (res.success) {
      // commit('setYoutubeDatas', res)
      // }

      return res
    },
    async signIn ({ commit }, data) {
      const res = await Api('http://18.141.178.15:8080/login').post(data)

      return res
    }
  },
  getters: {
    state (state) {
      return state
    }
  }
}
